import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-product-button',
	styleUrl: 'product-button.css'
})
export class ProductButton {
    @Prop({reflect: true}) icon: string;
    @Prop() alt: string;

	render() {
		if(this.icon)
			return <button><ks-img2 src={this.icon} alt={this.alt}/></button>;

		return <button><slot/></button>;
	}
}