ks-product-button {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 5px 2px;
    position: relative;
    z-index: 1;
}

ks-product-button button {
    cursor: pointer;

    border-style: none;
    border-radius: 50px;
    outline-offset: 4px;

    padding: 12px 25px;

    background-color: var(--color-dark);
    color: var(--text-color-dark);
    font-family: var(--product-button-font);
    font-size: var(--product-button-size);
    line-height: var(--product-button-size);
    

    transition: var(--transition-background-color);
}

ks-product-button button:hover,
ks-product-button button:focus {
    background-color: var(--color-dark-hover);
}
ks-product-button button:active {
    background-color: var(--color-dark-active);
}

@media only screen and (max-width: 400px) {
    ks-product-button button {
        padding: 12px 25px;
    }
}

ks-product-button[icon] {
    margin: 0;
}

ks-product-button[icon] button {
    width: 100%;
    border-radius: 0px;
    outline-offset: 0px;
    height: 40px;
    margin: 0;

    padding: 5px 0px;
}